import { FilterComponent } from './../../../../dialog/filter/filter.component';
import { Component, OnInit, ElementRef, ViewChild } from "@angular/core";
import { fuseAnimations } from "../../../../../core/animations";
import { FormBuilder, FormControl, FormGroup } from "@angular/forms";
import {
  MatPaginator,
  MatSort,
  MatDialogRef,
  MatDialog,
  PageEvent
} from "@angular/material";
import { FuseConfirmDialogComponent } from "../../../../../core/components/confirm-dialog/confirm-dialog.component";
import { AppConfig } from "../../../../shared/app.config";
import { DataSource } from "@angular/cdk/collections";
import { Observable } from "rxjs/Observable";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import "rxjs/add/operator/startWith";
import "rxjs/add/observable/merge";
import "rxjs/add/operator/map";
import "rxjs/add/operator/debounceTime";
import "rxjs/add/operator/distinctUntilChanged";
import "rxjs/add/observable/fromEvent";
import { FuseUtils } from "../../../../../core/fuseUtils";

import { BottlesService } from "../bottles.service";
import { ProgressBarService } from "../../../../../core/services/progress-bar.service";
import { countries } from "typed-countries";
import { map, startWith } from "rxjs/operators";
import { Shore } from "../../shores/shore.model";
import { ShoresService } from "../../shores/shores.service";

@Component({
  selector: "app-bottles-list",
  templateUrl: "./bottles-list.component.html",
  styleUrls: ["./bottles-list.component.scss"],
  animations: fuseAnimations
})
export class BottlesListComponent implements OnInit {

  chipsFilter = [];
  filtersObject = { "gender": "", "country": "", "shoreId": "", "createdFrom": "", "createdTo": "" }
  filterKey = { "gender": true, "country": true, "shoreId": true, "createdFrom": true, "createdTo": true }


  filteredOptions: Observable<string[]>;
  shores: Shore[] = [];
  searchInput: FormControl;
  dialogRef: any;

  dataSource: FilesDataSource | null;
  displayedColumns = [
    "thumbnail",
    "createdAt",
    "status",
    "owner",
    "gender",
    "country",
    "shore",
    "repliesUserCount",
    "bottleCompleteCount",
    "btns"
  ];

  @ViewChild(MatPaginator)
  paginator: MatPaginator;
  @ViewChild("filter")
  filter: ElementRef;
  @ViewChild(MatSort)
  sort: MatSort;

  defaultIcon: string = "";
  itemsCount: number = 0;

  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;

  constructor(
    private bottlesService: BottlesService,
    private progressBarService: ProgressBarService,
    public dialog: MatDialog,
    private formBuilder: FormBuilder,
    private shoresService: ShoresService
  ) {
    this.bottlesService.onItemsCountChanged.subscribe(
      count => (this.itemsCount = count)
    );
  }

  ngOnInit() {

    this.defaultIcon = AppConfig.defaultShoreIcon;

    this.dataSource = new FilesDataSource(
      this.bottlesService,
      this.paginator,
      this.sort
    );
    Observable.fromEvent(this.filter.nativeElement, "keyup")
      .debounceTime(150)
      .distinctUntilChanged()
      .subscribe(() => {
        if (!this.dataSource) {
          return;
        }
        this.dataSource.filter = this.filter.nativeElement.value;
      });




  }

  filterC(val: string): any[] {
    return countries.filter(option => option.iso.toLowerCase().includes(val));
  }

  clearFilter() {
    this.paginator.pageIndex = 0
    this.chipsFilter = []
    this.filtersObject = { "gender": "", "country": "", "shoreId": "", "createdFrom": "", "createdTo": "" }
    this.filter.nativeElement.value = "";
    this.bottlesService.getItemsCount("");
    this.getItemsPaging();
  }

  openFilter() {
    let dialogRef = this.dialog.open(FilterComponent, {
      width: '600px',
      data: { "filter": this.filtersObject, "filterKey": this.filterKey }
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.filtersObject = result;
        this.getItemsPaging(true);
        this.chipsFilter = [];
        for (let key in this.filtersObject) {
          if (this.filtersObject[key] != "") {
            var viewKey = key;
            if (key == "shoreId")
              viewKey = "shore";
            this.chipsFilter.push({ key: viewKey, value: this.filtersObject[key] });
          }
        }
      }
    });

  }


  getItemsPaging(isFilter = false) {
    if (isFilter) {
      this.paginator.pageIndex = 0
    }
    this.bottlesService
      .getItemsPaging(
        this.paginator.pageIndex,
        this.paginator.pageSize,
        this.filtersObject,
        this.filter.nativeElement.value
      )
      .then(items => {
        this.dataSource.connect();
      });
  }



  exportAsExcelFile(): void {
    this.bottlesService.export(this.filtersObject).then(res => {
      if (res) {
        console.log(res);
        window.location.href = res;
      }
    });
  }

  openNewTab(contact) {
    window.open(AppConfig.siteUrl + "bottles/view/" + contact.id)
  }

  isNewBottle(date) {
    var diff = Math.abs(new Date().getTime() - new Date(date).getTime()) / 3600000;
    if (diff > 24 * 7)
      return "oldBottle"
    else if (diff > 24)
      return "fewActiveBottle"
    else if (diff > 6)
      return "meniActiveBottle"
    else
      return "newBottle"
  }

  deleteItem(contact) {
    this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
      disableClose: false
    });

    this.confirmDialogRef.componentInstance.confirmMessage =
      "Are you sure you want to delete?";
    this.progressBarService.toggle();
    this.confirmDialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.bottlesService.deleteItem(contact);
        // this.itemsCount--;
        this.getItemsPaging();
      }
      this.confirmDialogRef = null;
    });
  }
}

export class FilesDataSource extends DataSource<any> {
  _filterChange = new BehaviorSubject("");
  _filteredDataChange = new BehaviorSubject("");

  get filteredData(): any {
    return this._filteredDataChange.value;
  }

  set filteredData(value: any) {
    this._filteredDataChange.next(value);
  }

  get filter(): string {
    return this._filterChange.value;
  }

  set filter(filter: string) {
    this._filterChange.next(filter);
  }



  constructor(
    private bottlesService: BottlesService,
    private _paginator: MatPaginator,
    private _sort: MatSort
  ) {
    super();
    this.filteredData = this.bottlesService.items;
  }

  /** Connect function called by the table to retrieve one stream containing the data to render. */
  connect(): Observable<any[]> {
    const displayDataChanges = [
      this.bottlesService.onItemsChanged,
      this._paginator.page,
      this._filterChange,
      this._sort.sortChange
    ];

    return Observable.merge(...displayDataChanges).map(() => {
      let data = this.bottlesService.items;

      data = this.filterData(data);

      this.filteredData = [...data];

      data = this.sortData(data);

      const startIndex = this._paginator.pageIndex * this._paginator.pageSize;

      return data;
    });
  }

  filterData(data) {
    if (!this.filter) {
      return data;
    }
    return FuseUtils.filterArrayByString(data, this.filter);
  }

  sortData(data): any[] {
    if (!this._sort.active || this._sort.direction === "") {
      return data;
    }

    return data.sort((a, b) => {
      let propertyA: number | string = "";
      let propertyB: number | string = "";

      switch (this._sort.active) {
        case "createdAt":
          [propertyA, propertyB] = [a.createdAt, b.createdAt];
          break;
        case "weight":
          [propertyA, propertyB] = [a.weight, b.weight];
          break;
        case "status":
          [propertyA, propertyB] = [a.status, b.status];
          break;
        case "shore":
          [propertyA, propertyB] = [a.shore.name_en, b.shore.name_en];
          break;
        case "owner":
          [propertyA, propertyB] = [a.userneme, b.userneme];
          break;
      }

      const valueA = isNaN(+propertyA) ? propertyA : +propertyA;
      const valueB = isNaN(+propertyB) ? propertyB : +propertyB;

      return (
        (valueA < valueB ? -1 : 1) * (this._sort.direction === "asc" ? 1 : -1)
      );
    });
  }

  disconnect() { }
}



